}
@font-face {
  font-family: "antonio-regular";
  src: url("fonts/antonio/antonio-regular-webfont.woff2") format("woff2"), url("fonts/antonio/antonio-regular-webfont.woff") format("woff");
}
@font-face {
  font-family: "great-vibes-regular";
  src: url("fonts/great_vibes/great-vibes-regular-webfont.woff2") format("woff2"), url("fonts/great_vibes/great-vibes-regular-webfont.woff") format("woff");
}
@font-face {
  font-family: "lora-bold-italic";
  src: url("fonts/lora/lora-bold-italic-webfont.woff2") format("woff2"), url("fonts/lora/lora-bold-italic-webfont.woff") format("woff");
}
@font-face {
  font-family: "lora-bold";
  src: url("fonts/lora/lora-bold-webfont.woff2") format("woff2"), url("fonts/lora/lora-bold-webfont.woff") format("woff");
}
@font-face {
  font-family: "lora-italic";
  src: url("fonts/lora/lora-italic-webfont.woff2") format("woff2"), url("fonts/lora/lora-italic-webfont.woff") format("woff");
}
@font-face {
  font-family: "lora-regular";
  src: url("fonts/lora/lora-regular-webfont.woff2") format("woff2"), url("fonts/lora/lora-regular-webfont.woff") format("woff");
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
}
body {
  line-height: 1;
  background-color: #fff;
  text-rendering: optimizeLegibility;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
}
:focus {
  outline: none;
}
p,
dd,
dl,
figure,
blockquote {
  margin: 0;
}
blockquote,
q {
  quotes: none;
}
ul,
ol {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}
audio,
video {
  display: block;
}
img {
  display: inline-block;
  border: none;
}
iframe {
  border: none;
}
pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: inherit;
}
a {
  background-color: transparent;
  text-decoration: none;
  color: inherit;
}
abbr {
  border: none;
  text-decoration: none;
}
b,
strong {
  font-weight: inherit;
}
i,
em {
  font-style: inherit;
}
dfn {
  font-style: inherit;
}
mark {
  background-color: transparent;
  color: inherit;
}
small {
  font-size: inherit;
}
button,
input,
optgroup,
select,
textarea {
  padding: 0;
  margin: 0;
  border: none;
  border-radius: 0;
  box-shadow: none;
  background-color: transparent;
  font: inherit;
  color: inherit;
  letter-spacing: inherit;
}
button,
input {
  overflow: visible;
}
button,
select {
  text-align: left;
  text-transform: none;
}
button,
[type='button'],
[type='reset'],
[type='submit'] {
  cursor: pointer;
  appearance: none;
}
textarea {
  resize: none;
  overflow-y: auto;
  overflow-x: hidden;
}
button::-moz-focus-inner,
[type='button']::-moz-focus-inner,
[type='reset']::-moz-focus-inner,
[type='submit']::-moz-focus-inner {
  border: none;
  padding: 0;
}
button:-moz-focusring,
[type='button']:-moz-focusring,
[type='reset']:-moz-focusring,
[type='submit']:-moz-focusring {
  outline: none;
}
[type='number']::-webkit-inner-spin-button,
[type='number']::-webkit-outer-spin-button {
  height: auto;
}
[type='search']::-webkit-search-decoration {
  appearance: none;
}
[type='search'] {
  outline: none;
}
fieldset {
  padding: 0;
  margin: 0;
  border: none;
}
legend {
  display: block;
  padding: 0;
  white-space: normal;
}
select {
  appearance: none;
}
select::-ms-expand {
  display: none;
}
::-webkit-input-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity 0.3s;
}
::-moz-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity 0.3s;
}
:-moz-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity 0.3s;
}
:-ms-input-placeholder {
  color: inherit;
  opacity: 1;
  transition: opacity 0.3s;
}
:focus::-webkit-input-placeholder {
  opacity: 0;
}
:focus::-moz-placeholder {
  opacity: 0;
}
:focus:-moz-placeholder {
  opacity: 0;
}
:focus:-ms-input-placeholder {
  opacity: 0;
}
svg {
  display: block;
  width: 100%;
  height: 100%;
}
[hidden] {
  display: none;
}
:disabled,
.disabled {
  cursor: not-allowed;
}
::-ms-clear {
  display: none;
}
.visually-hidden {
  position: absolute;
  z-index: -$z-index1;
  width: 0;
  height: 0;
  margin: 0;
  border: none;
  overflow: hidden;
}
html {
  font: 14px/24px 'lora-regular', sans-serif, arial;
  color: #797979;
}
.container {
  width: 98%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}
h1 {
  font: 48px/60px 'antonio-regular', sans-serif, arial;
  color: #797979;
}
h2 {
  font: 48px/60px 'antonio-regular', sans-serif, arial;
  color: #797979;
}
